import router from './router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { base } from '@/router/utils'

NProgress.configure( { showSpinner: false } )

import { useAuthStore, useAppStore, useUserStore } from '@/stores'
import i18n from '@/locales'
const { t } = i18n.global

/* axios service */
import { Server } from '@axios'

/*  */
Server.interceptors.request.use( ( config: any ) => {
  //  pinia  
  let token = useUserStore().token
  if ( token ) {
    // token
    config.headers[ 'Authorization' ] = token
  }
  return config
}, (er: any) => {
  return Promise.reject( er )
} )

/*  */
Server.interceptors.response.use( ( response: any ) => {
  // 20010020
  if ( response.data?.code === 20010090 ) {
    useUserStore().resetToken().then( () => {
      window.location.reload()
    } )
  }
  return response
}, ( er: any ) => {
	return Promise.reject( er )
} )

/*  */
router.beforeEach( async( to, _from, next ) => {
  // 
  NProgress.start()

  // 
  const dom: any = document
  const title = to.meta.title
  const appStore = useAppStore()
  if ( appStore.i18n ) {
    dom.title = title ? t( `route.${ title }` ).toUpperCase() : 'loading...'
  } else {
    dom.title = title
  }

  const userStore = useUserStore()
  // 
  const token = userStore.token
  const loginPath = `${ base }/login`
  if ( token ) {
    if ( to.path == loginPath ) {
      // 
      next( { path: `${ base }/` } )
      NProgress.done()
    } else {
      //  
      if ( to.path == `${ base }/index` ) {
        // 
        next( { path: `${ base }/` } )
        NProgress.done()
        return
      }

      // 
      const hasPowers = userStore.powers && userStore.powers.length > 0

      if ( hasPowers ) {
        next()
      } else {
        try {
          // 
          const { permissions } = await userStore.getUserInfo()

          const authStore = useAuthStore()
          // 
          const accessRoutes = await authStore.generateRoutes( permissions )
          // 
          accessRoutes.forEach( ( route: any ) => {
            const hasRoute = router.hasRoute( route.name )
            if ( !hasRoute ) {
              router.addRoute( route )
            }
          } )
          
          next( { ...to, replace: true } )
        } catch ( error ) {
          console.log('', error)
          await userStore.resetToken()
          next( `${ loginPath }?redirect=${ to.path }` )
          NProgress.done()
        }
      }
    }
  } else {
    if ( to.path == loginPath ) {
      next()
    } else {
      // 
      next( `${ loginPath }?redirect=${ to.path }` )
      NProgress.done()
    }
  }
} )

router.afterEach( () => {
  NProgress.done()
} )
