/**
 * [isFullscreenEnabled H5 API]
 * @return { boolean } [true,false]
 */
export const isFullscreenEnabled = (): boolean => {
  const dom: any = window.document
  return dom.fullscreenEnabled ||
    dom.mozFullScreenEnabled ||
    dom.webkitFullscreenEnabled ||
    dom.msFullscreenEnabled || false
}

/**
 * h5 api
 * @param { Document | HTMLElement } element dom
 * @example
 * setFullscreen( document )
 */
export const setFullscreen = ( element: Document | HTMLElement ) => {
  const el: any = element instanceof HTMLElement ? element : document.documentElement
  const win: any = window
  const rfs = el.requestFullscreen ||
    el.webkitRequestFullscreen ||
    el.mozRequestFullScreen ||
    el.msRequestFullscreen
  if ( rfs ) {
    rfs.call( el )
  } else if ( win.ActiveXObject ) {
    let ws = new win.ActiveXObject( 'WScript.Shell' )
    ws && ws.SendKeys( '{F11}' )
  } else {
    console.error( '' )
  }
}

/**
 * ()
 * @return { boolean } [fullscreenElement,false]
 */
export const isFullscreen = (): boolean => {
  const dom: any = window.document
  return !!dom.fullscreenElement ||
    !!dom.msFullscreenElement ||
    !!dom.mozFullScreenElement ||
    !!dom.webkitFullscreenElement || false
}

/**
 * h5 API
 */
export const exitFullscreen = () => {
  const dom: any = document
  const win: any = window
  const efs = dom.exitFullscreen ||
    dom.webkitExitFullscreen ||
    dom.mozCancelFullScreen ||
    dom.msExitFullscreen;
  if ( efs ) {
    efs.call( document )
  } else if ( win.ActiveXObject ) {
    var ws = new win.ActiveXObject( 'WScript.Shell' )
    ws && ws.SendKeys( '{F11}' )
  } else {
    console.error( '' )
  }
}

/**
 * 
 * @return { Object } 
 */
export const getSearchParams = (): any => {
  let url = decodeURI( location.search ) // url?
  let obj: any = {} // 
  let str // 
  if ( url.charAt( 0 ) == '?' ) {
    url = url.substring( 1, url.length )

    // 
    if ( url.indexOf( '&' ) != -1 ) {
      str = url.split( '&' )
      for ( let i = 0; i < str.length; i++ ) {
        let str1 = str[ i ].split( '=' )
        if ( str1.length == 2 && str1[ 0 ] != '' ) {
          obj[ str1[ 0 ] ] = str1[ 1 ]
        }
      }
    }
    // 
    else {
      str = url.split( '=' )
      if ( str.length == 2 && str[ 0 ] != '' ) {
        obj[ str[ 0 ] ] = str[ 1 ]
      }
    }
  }
  return obj
}


/**
 * IsPC  PC
 * @return { boolean } 
 */
export const IsPC = (): boolean => {
  let userAgentInfo = navigator.userAgent
  let Agents = [
    'Android', 'iPhone',
    'SymbianOS', 'Windows Phone',
    'iPad', 'iPod'
  ]
  let flag = true
  for ( let v = 0; v < Agents.length; v++ ) {
    if ( userAgentInfo.indexOf( Agents[ v ] ) > 0 ) {
      flag = false
      break
    }
  }
  return flag
}

/**
 * 
 * @return { string } 
 */
export const getBrowser = (): string => {
  let UserAgent = navigator.userAgent.toLowerCase()
  let browser = '', win: any = window
  let browserArray: any = {
    IE: win.ActiveXObject || 'ActiveXObject' in win, // IE
    Chrome: UserAgent.indexOf( 'chrome' ) > -1 && UserAgent.indexOf( 'safari' ) > -1, // Chrome
    Firefox: UserAgent.indexOf( 'firefox' ) > -1, // 
    Opera: UserAgent.indexOf( 'opera' ) > -1, // Opera
    Safari: UserAgent.indexOf( 'safari' ) > -1 && UserAgent.indexOf( 'chrome' ) == -1, // safari
    Edge: UserAgent.indexOf( 'edge' ) > -1, // Edge
    QQBrowser: /qqbrowser/.test(UserAgent ), // qq
    WeixinBrowser: /MicroMessenger/i.test(UserAgent ) // 
  }
  for ( let i in browserArray ) {
    if ( browserArray[ i ] ) {
      browser = i
    }
  }
  return browser
}

/**
 * 
 * @return { boolean } 
 */
export const isWeiXinBrowser = (): boolean => {
  let ua: any = window.navigator.userAgent.toLowerCase()
  // console.log( ua)//mozilla/5.0 ( iphone cpu iphone os 9_1 like mac os x) applewebkit/601.1.46 (khtml, like gecko)version/9.0 mobile/13b143 safari/601.1
  if ( ua.match( /MicroMessenger/i ) == 'micromessenger' ) {
    return true
  } else {
    return false
  }
}

/**
 * 
 * @return { string } 
 */
export const getNetworkType = (): string => {
  const navigator = <any>window.navigator
  // navigator
  if ( navigator && navigator.connection ) {
    // connection
    var connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection;

    // connectioneffectiveType
    if ( connection && connection.effectiveType ) {
      return connection.effectiveType;
    }
  } // 
  return 'unknown'
}

/**
 * 
 * @param { string } title 
 * @example
 * setDocumentTitle( '' )
 */
export const setDocumentTitle = ( title: string ) => {
  document.title = title || document.title
  let ua: any = navigator.userAgent.toLowerCase()
  if( ua.match( /MicroMessenger/i)== 'micromessenger' && !!ua.match( /\( i[^]+( U)? CPU.+Mac OS X/i ) ){
    let ifr = document.createElement( 'iframe' )
    ifr.src = './favicon.ico'
    ifr.onload = () => {
      setTimeout(() => {
        ifr.remove()
      }, 0 )
    }
    document.body.appendChild( ifr )
  }
}


/**
 * Url
 * @param { string } name 
 * @return { string } 
 */
export const getUrlParam = ( name: string ): string => {
	let reg: any = new RegExp( '(^|&)' + name + '=([^&]*)(&|$)', 'i' )
	let r = window.location.search.substr( 1 ).match( reg )
	let context = ''
	if ( r != null )
		context = r[ 2 ]
	reg = null
	r = null
	return ( context == null || context == '' || context == 'undefined' ) ? '' : context
}


/**
 * 
 * @param { any } obj 
 * @return { string } 
 * @example
 * getObjectType( {} )
 */
export const getObjectType = ( obj: any ): string => {
  const toString = Object.prototype.toString
  const map: Object = {
    '[object Boolean]': 'boolean',
    '[object Number]': 'number',
    '[object String]': 'string',
    '[object Function]': 'function',
    '[object Array]': 'array',
    '[object Date]': 'date',
    '[object RegExp]': 'regExp',
    '[object Undefined]': 'undefined',
    '[object Null]': 'null',
    '[object Object]': 'object'
  }
  if ( obj instanceof Element ) {
    return 'element'
  }
  return map[ toString.call( obj ) ]
}

/**
 * 
 * @param { Object } obj1  1
 * @param { Object } obj2  2
 * @return { boolean } 
 * @example
 * ifCompare( { a: 1 , b: 2 }, { a: 1 } )
 */
export const ifCompare = ( obj1, obj2 ): boolean => {
  const o1keys = Object.keys( obj1 )
  const o2keys = Object.keys( obj2 )
  if ( o1keys.length !== o2keys.length ) return false
  for ( let i = 0; i < o1keys.length; i++ ) {
    const key = o1keys[ i ]
    if ( !o2keys.includes( key ) ) return false
    if ( obj1[ key ] !== obj2[ key ] ) return false
  }
  return true
}