const chnNumChar = [ '', '', '', '', '', '', '', '', '', '' ]
const chnUnitSection = [ '', '', '', '', '' ]
const chnUnitChar = [ '', '', '', '' ]
const numToChn = ( num: number ) => {
  let index = num.toString().indexOf( '.' )
  if ( index != -1 ) {
    let str = num.toString().slice( index )
    let a = ''
    for ( let i = 1; i < str.length; i++ ) {
      a += chnNumChar[ parseInt( str[ i ] ) ]
    }
    return a
  } else {
    return ''
  }
}

// 
const sectionToChinese = ( section: number ) => {
  // zero 
  let str = '', chnstr = '', zero = false, count = 0 
  while ( section > 0 ) {
    // 10
    let v = section % 10
    // 
    if ( v == 0 ) {
      // 0
      if ( zero ) {
        zero = false
        chnstr = chnNumChar[ v ] + chnstr
      }
    } else {
      // 
      zero = true
      str = chnNumChar[ v ]
      str += chnUnitChar[ count ]
      chnstr = str + chnstr
    }
    count++
    section = Math.floor( section / 10 )
  }
  return chnstr
}

/**
 * 
 * @param { number } num 
 * @return { string } 
 * @example
 * numberToChinese( 123456789 )
 */
export const numberToChinese = ( num: number ): string => {
  let a = numToChn( num )
  num = Math.floor( num )
  let unitPos = 0
  let strIns = '', chnStr = ''
  let needZero = false
  if ( num === 0 ) {
    return chnNumChar[ 0 ]
  }
  while ( num > 0 ) {
    let section = num % 10000
    if ( needZero ) {
      chnStr = chnNumChar[ 0 ] + chnStr
    }
    strIns = sectionToChinese( section )
    strIns += ( section !== 0 ) ? chnUnitSection[ unitPos ] : chnUnitSection[ 0 ]
    chnStr = strIns + chnStr
    needZero = ( section < 1000 ) && ( section > 0 )
    num = Math.floor( num / 10000 )
    unitPos++
  }
  return chnStr + a
}


/**
 * 
 * @param { number } money 
 * @return { string } 
 * @example
 * convertCurrency( 123456789.98 )
 */
export const convertCurrency = ( money: number ): string => {
  //
  let cnNums = new Array( '', '', '', '', '', '', '', '', '', '' )
  //
  let cnIntRadice = new Array( '', '', '', '' )
  //
  let cnIntUnits = new Array( '', '', '', '' )
  //
  let cnDecUnits = new Array( '', '', '', '' )
  //
  let cnInteger = ''
  //
  let cnIntLast = ''
  //
  let maxNum = 999999999999999.9999
  //
  let integerNum
  //
  let decimalNum
  //
  let chineseStr = ''
  //
  let parts
  if ( money >= maxNum ) {
    return ''
  }
  // 0
  if ( money == 0 ) {
    chineseStr = cnNums[ 0 ] + cnIntLast + cnInteger
    return chineseStr
  }
  // 
  let _money = money.toString()
  // indexOf  0  -1 
  if ( _money.indexOf( '.' ) == -1 ) {
    integerNum = _money
    decimalNum = ''
  } else {
    parts = _money.split( '.' )
    integerNum = parts[ 0 ]
    decimalNum = parts[ 1 ].substring( 0, 4 )
  }
  //
  if ( parseInt( integerNum, 10 ) > 0 ) {
    let zeroCount  = 0
    let IntLen = integerNum.length
    for ( let i = 0; i < IntLen; i++ ) {
      let n = integerNum.substring( i, i+1 )
      let p = IntLen - i - 1
      let q = p / 4
      let m = p % 4
      if (  n == '0' ) {
        zeroCount ++
      } else {
        if ( zeroCount > 0 ) {
          chineseStr += cnNums[ 0 ]
        }
        zeroCount = 0
        chineseStr += cnNums[ parseInt( n ) ] + cnIntRadice[ m ]
      }
      if ( m == 0 && zeroCount < 4 ) {
        chineseStr += cnIntUnits[ q ]
      }
    }
    // + 
    chineseStr += cnIntLast
  }
  // 
  if ( decimalNum != '' ) {
    let decLen = decimalNum.length
    for ( let i = 0; i < decLen; i++ ) {
      let n = decimalNum.substring( i, i + 1 )
      if ( n != '0' ) {
        chineseStr += cnNums[ Number( n ) ] + cnDecUnits[ i ]
      }
    }
  }
  if ( chineseStr == '' ) {
    chineseStr += cnNums[ 0 ] + cnIntLast + cnInteger
  } else if ( decimalNum == '' ) {
    chineseStr += cnInteger
  }
  return chineseStr
}
