/**
 *  
 * @param { Array } list 
 * @param { number } len   3
 * @return { Array } 
 * @example
 * arraySlice( [ 1, 2, 3, 4 ], 2 )
 */
export const arraySlice = ( list: Array<any>, len: number = 3 ): Array<any> => {
  let l = list.length
  if ( l <= len ) return [ list ]
  let n = l % len == 0 ? l / len : Math.floor ( l / len ) + 1
  let lt: any[] = []
  for ( let index = 0; index < n; index++ ) {
    const s: number = index * len, e: number = ( index + 1 ) * len 
    lt.push( list.slice( s, e ) )
  }
  return lt
}


interface RepObj {
  value: number
  count: number
}
/**
 * 
 * @param { Array } list 
 * @return { Array } 
 * @example
 * numArrayFindRep( [ 1, 1, 2, 3 ] )
 */
export const numArrayFindRep = ( list: Array<number> ): Array<RepObj> => {
  let l: any[] = [], s: any[] = []
  list.forEach( ( val, _n ) => {
    if ( !s[ val ] ) {
      s[ val ] = val
      l.push( {
        value: val,
        count: 1
      } )
    } else {
      l.forEach( ( _val, _n ) => {
        if ( _val.val == val ) {
          _val.num++
        }
      } )
    }
  } )
  return l
}


interface Route {
  path: string
  children?: Array<Route>
}
/**
 * 
 * @param { Array } routes 
 * @param { string } routes.path 
 * @param { Array } routes.children 
 * @param { string } path 
 * @return { Object }
 * @example
 * infiniteFindRoutePath( [], '/path' )
 */
export const infiniteFindRoutePath = ( routes: Array<Route>, path: string ): Route | undefined => {
  // 
  const findChile = ( child: Route ) => {
    if ( child.children ) {
      let o = child.children.find( ( it ) => {
        if ( it.path == path ) {
          return true
        } else {
          let o = findChile( it )
          if ( o ) return true
        }
      } )
      if ( o ) return true
    } else {
      return false
    }
  }

  // id
  return routes.find( item => {
    if ( item.path == path ) {
      return true
    } else {
      let o = findChile( item )
      if ( o ) return true
    }
  } )
}

/**
 * 
 * @param { any } obj 
 * @return { any }
 * @example
 * deepCopy( { a: 1 } )
 */
export const deepCopy = ( obj: any ): any => {
  let newobj: any = obj.constructor === Array ? [] : {}
  if ( typeof obj !== 'object' ) {
    return obj
  } else if ( window.JSON) {
    // 
    let str = JSON.stringify( obj )
    // 
    newobj = JSON.parse( str )
  } else {
    for ( var i in obj ) {
      newobj[ i ] = typeof obj[ i ] === 'object' ? deepCopy( obj[ i ] ) : obj[ i ]
    }
  }
  return newobj
}
