/**
 * 
 * @param { string } code 
 * @return { boolean } 
 * @example
 */
export const checkCreditCode = ( code: string ): boolean => {
  !code && ( code = '' )
  let regex = /^[^_IOZSVa-z\W]{2}\d{6}[^_IOZSVa-z\W]{10}$/g
  if ( !regex.test( code ) ) return false
  return true
}
